import {
	ajax
} from '../../common/js/util.js'
import lotusAddress from "../../components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue"
import jweixin from '../../common/js/wx.js'
import {
	Toast
} from 'vant'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			type: false,
			cardType: 0,
			cards: 0,
			user: '',
			tel: '',
			card: '',
			address: '',
			total: 0,
			timer: null,
			lotusAddressData: {
				visible: false,
				provinceName: '',
				cityName: '',
				townName: ''
			},
			region: '',
			checkbox: [{
				checked: false,
				value: 'A'
			}]
		}
	},
	components: {
		"lotus-address": lotusAddress,
		Toast
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.cards = option.type
		this.cardType = option.cardType
		this.total = option.price
		if (this.$store.state.vipType == 0) {
			this.type = false
		} else {
			this.type = true
			this.init()
		}
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				uid: this.$store.state.uid
			}
			let data = {
				url: '/api/gzh/vipCard',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.user = res.data.username
						self.tel = res.data.phone
						self.card = res.data.idNumber
						self.region = res.data.city
						self.address = res.data.address
					}
				}
			}
			ajax(data)
		},
		//打开picker
		openPicker() {
			this.lotusAddressData.visible = true;
		},
		//回传已选的省市区的值
		choseValue(res) {
			this.lotusAddressData.visible = res.visible; //visible为显示与关闭组件标识true显示false隐藏
			//res.isChose = 1省市区已选 res.isChose = 0;未选
			if (res.isChose) {
				this.lotusAddressData.provinceName = res.provice; //省
				this.lotusAddressData.cityName = res.city; //市
				this.lotusAddressData.townName = res.town; //区
				this.region = `${res.provice} ${res.city} ${res.town}`; //region为已选的省市区的值
			}
			console.log(this.region)
		},
		CheckboxChange(e) {
			var items = this.checkbox,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				items[i].checked = false;
				for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
					if (items[i].value == values[j]) {
						items[i].checked = true;
						break
					}
				}
			}
			console.log(this.checkbox)
		},
		goIdea() {
			uni.navigateTo({
				url: '../content/content?url=' + encodeURIComponent('http://m.xgcyz1978.com/display/agreement?id=5')
			})
		},
		onUnload() {
			uni.hideLoading()
		},
		paySuc() {
			let self = this

			if (this.user == '') {
				Toast('姓名不能为空')
				return
			}

			if (this.tel == '') {
				Toast('手机号不能为空')
				return
			} else if (!(/^1[3456789]\d{9}$/.test(this.tel))) {
				Toast('手机号格式不正确')
				return
			}

			let idcardReg =
				/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
			if (this.card == '') {
				Toast('身份证不能为空')
				return
			} else if (!(idcardReg.test(this.card))) {
				Toast('身份证不合法')
				return
			}

			if (this.type == 0) {
				if (this.city == '') {
					Toast('请选择地址')
					return
				}

				if (this.address == '') {
					Toast('请填写地址详情')
					return
				}
			}

			if (!this.checkbox[0].checked) {
				Toast('请阅读协议')
				return
			}

			let datas = {
				uid: this.$store.state.uid,
				type: parseInt(this.cards) + 1,
				cardType: this.cardType,
				username: this.user,
				phone: this.tel,
				idNumber: this.card,
				city: this.region,
				address: this.address,
				amount: this.total
			}
			console.log(datas)
			let data = {
				url: '/api/gzh/addVip',
				data: datas,
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						let orderId = res.data.orderId
						let appId = res.data.body.appId
						let timeStamp = res.data.body.timeStamp
						let nonceStr = res.data.body.nonceStr
						let packages = res.data.body.prepay
						let signType = res.data.body.signType
						let paySign = res.data.body.paySign
						self.onBridgeReady(appId, timeStamp, nonceStr, packages, signType, paySign, orderId)
					} else {
						Toast(res.data.resultNote)
					}
				},
				fail: function(res) {
					Toast(res.data.resultNote)
				}
			}
			ajax(data)
		},
		onBridgeReady(appId, timeStamp, nonceStr, packages, signType, paySign, orderId) {
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					'appId': appId,
					'timeStamp': timeStamp,
					'nonceStr': nonceStr,
					'package': packages,
					'signType': signType,
					'paySign': paySign
				},
				function(res) {
					if (res.err_msg === 'get_brand_wcpay_request:ok') {
						uni.showLoading({
							title: '加载中',
							mask: true
						})
						let timer = setInterval(function(){
							let forms = {
								orderId: orderId
							}
							let look = {
								url: '/api/gzh/getCardOrderState',
								data: forms,
								success: function(res) {
									if (res.data.result == 0) {
										if (res.data.state == 1) {
											uni.hideLoading()
											clearInterval(timer)
											uni.navigateTo({
												url: '../sucResult/sucResult?type=' + res.data.cardType + '&time=' + res.data.endDate
											})
										} 
									}
								}
							}
							ajax(look)
						},200)
					} else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
						Toast('用户取消支付')
					} else if (res.err_msg === 'get_brand_wcpay_request:fail') {
						Toast('网络异常，请重试')
					}
				}
			)
		}
	}
}
